import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'filterDataPipe',
    pure: false
})

export class FilterDataPipe implements PipeTransform {
    transform(items:any[], args1: string, args2: string, args3: string):any[] {
        return items;
    }
}



